<template>
  <div class="scrollBox">
    <div class="serarch-bar" :class="{isFocus: isFocus || content}">
      <div class="backButton" @click="routeBack"></div>
      <div class="search-content">
        <div class="search-icon" @click="search">
          <img src="../../assets/img/movie/white_search.png" alt="搜索">
        </div>
        <input class="searchInput" ref="searchInput" @keyup.enter="search" @focus="focusFn" @input="getSearchTip" @blur="blurFn" maxlength="30" v-model="content" type="text" :placeholder="placeholderText">
        <div class="clearInput" v-show='content.length > 0' @click="clearInput"></div>
      </div>
      <div class="searchButton" @click="search">搜索</div>
    </div>
    <div id="articleSearch" ref="articleSearch">
      <!-- 搜索关键字关联 -->
      <div class="searchTip" v-if="loading === 0 && isFocus && list.length === 0">
        <div class="search-item" v-for="(item, index) in searchTip" :key="index" @click="setKwTip(index)">
          <div class="text" v-html="item.title"></div>
        </div>
      </div>
      <video-loading v-show="loading === 1 && page === 1"></video-loading>
      <!-- 有搜索内容 -->
      <div class="scrollTouch" v-infinite-scroll="loadMore" infinite-scroll-distance="100">
        <div class="list" v-if="list.length > 0">
          <news-list :list="list"></news-list>
          <loading v-if="loading === 1" :loading='loading' :page="page"></loading>
          <div class="no-list-data" v-else-if="loading === 2">{{loading === 1 ? '加载中...' : '没有更多数据了！'}}</div>
        </div>
        <!-- 无搜索内容时 -->
        <div class="others" v-if="list.length === 0 && !content">
          <div class="history" v-if="commonState.searchList.length > 0 && searchTip.length === 0">
            <div class="history-title flex-row jusB">
              <div class="text">历史记录</div>
              <div class="rightIcon" @click="showConfirm = true">
                <img src="../../assets/img/movie/delete.png" alt="删除">
              </div>
            </div>
            <div class="history-list">
              <div class="history-item" v-for="(item, index) in commonState.searchList" @click="setHistory(item)" :key="index">
                <div class="timeIcon iconSize">
                  <img src="../../assets/img/task/time_icon.png" alt="时间">
                </div>
                <div class="text">{{item}}</div>
              </div>
            </div>
          </div>
          <div class="no-history" v-if="commonState.searchList.length === 0 && searchTip.length === 0 && loading === 0 && list.length === 0">
            <div class="iconSize list-image">
              <img src="../../assets/img/movie/movie-search.png" alt="">
            </div>
            <div class="text">暂无搜索记录～</div>
          </div>
        </div>
        <prompt-box
          :dropBol="showConfirm"
          @leftBtn="showConfirm=false"
          @rightBtn="clearHistory"
          rightTxt="清空"
          contentTxt="确认清空输入记录？"
        >
        </prompt-box>
      </div>
      <!-- 加载中 -->
      <div class="noResult" v-show="loading === 2 && list.length === 0">
        <div class="iconSize">
          <img src="../../assets/img/movie/movie_article.png" alt="图标">
        </div>
        <div class="text">搜索不到相关资讯<br>换个关键词试试吧</div>
      </div>
    </div>
  </div>
  
</template>
<script>
import NewsList from '../common/news-list'
import PromptBox from '../common/prompt-box'
import VideoLoading from '@/components/movie/common/video-loading'
import Loading from '@/components/movie/common/list-loading'
export default {
  components: {
    NewsList,
    PromptBox,
    VideoLoading,
    Loading
  },
  data () {
    return {
      placeholderText: '搜索你感兴趣的内容',
      content: '',
      list: [],
      searchTip: [],
      searchTemp: [],
      page: 1,
      loading: 0,
      showConfirm: false,
      isFocus: false,
      searchTipLoading: 0
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  methods: {
    setHistory (item) {
      this.content = item
      this.search()
    },
    // 设置关键字 由于文本替换成html，所以不取已经替换的html
    setKwTip (index) {
      this.content = this.searchTemp[index].title
      this.search()
    },
    focusFn () {
      this.isFocus = true
    },
    blurFn () {
      this.isFocus = false
    },
    getSearchTip () {
      if (!this.content || this.searchTipLoading > 0) {
        this.searchTipLoading = 0
        return
      }
      this.searchTipLoading = 1
      // 默认500ms之后无响应
      setTimeout(() => {
        this.searchTipLoading = 0
      }, 500)
      if (this.tipTimer) {
        clearTimeout(this.tipTimer)
      }
      this.tipTimer = setTimeout(() => {
        let data = {
          kw: this.content
        }
        if (!data.kw) {
          this.searchTipLoading = 0
          return
        }
        this.$api.get('ARTICLE_SEARCH_TIP', data, res => {
          this.searchTipLoading = 0
          this.searchTemp = JSON.parse(JSON.stringify(res.data))
          let reg = new RegExp(`${this.content}`)
          for (let v of res.data) {
            v.title = v.title.replace(reg, `<span class="magic">${this.content}</span>`)
          }
          this.searchTip = res.data
        })
      }, 300)
    },
    // 清空历史记录
    clearHistory () {
      this.$store.commit('common_set_searchList', {type: 1})
      this.showConfirm = false
      this.$toast({
        message: '删除成功',
        duration: 1500
      })
    },
    loadMore () {
      if (this.loading > 0 || !this.content) {
        return
      }
      this.loading = 1
      this.getSearchResult(list => {
        this.list = [...this.list, ...list]
      })
    },
    // 搜索历史记录
    searchHistory () {

    },
    // 搜索按钮
    search () {
      if (!this.content) {
        this.searchTip = []
        return
      }
      this.loading = 1
      this.page = 1
      this.$store.commit('common_set_searchList', {val: this.content})
      this.getSearchResult(list => {
        this.list = list
      })
    },
    clearInput () {
      this.content = ''
      this.page = 1
      this.list = []
    },
    getSearchResult (cb) {
      let data = {
        kw: this.content,
        page: this.page
      }
      this.$api.get('ARTICLA_SEARCH', data, res => {
        let reg = new RegExp(`${data.kw}`, 'g')
        if (res.data.data) {
          for (let v of res.data.data) {
            v.title = v.title.replace(reg, `<span class="magic">${data.kw}</span>`)
          }
        }
        cb && cb(res.data.data)
        this.loading = 0
        if (this.page >= res.data.last_page) {
          this.loading = 2
        }
        this.page++
      })
    },
    routeBack () {
      this.$router.go(-1)
    }
  },
  watch: {
    $route (to, from) {
      if (from.name !== 'ArticleDetails' && from.name !== 'ArticleSearch') {
        this.content = ''
        this.list = []
        this.loading = 0
        this.$refs.searchInput.focus()
      }
    },
    content (kw) {
      this.list = []
      this.loading = 0
      if (!kw) {
        this.searchTip = []
      }
    }
  },
  mounted () {
    this.$utils.cache('articleSearch', 0)
  },
  beforeRouteLeave (to, from, next) {
    this.$utils.cache('articleSearch', this.$refs.articleSearch.scrollTop)
    next()
  },
  activated () {
    this.$nextTick(() => {
      this.$refs.articleSearch.scrollTop = this.$utils.cache('articleSearch')
    })
  }
}
</script>
<style lang="scss" scoped>
.scrollBox {
  height: 100vh;
  overflow: hidden;
  &.Router {
    transition: all 0s ease!important;
  }
  .serarch-bar {
    height: 88px;
    position: relative;
    display: flex;
    background: #ffaa00;
    align-items: center;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    z-index: 1;
    &.isFocus {
      .search-content {
        .search-icon {
          margin-left: 0px;
        }
      }
    }
    .backButton {
      left: 0;
      width: 74px;
      height: 100%;
      top: 0;
      background: url('../../assets/img/white_back.png') no-repeat center;
      background-size: 20px 35px;
    }
    .search-content {
      position: relative;
      flex: 1;
      background: #e49c0b;
      border-radius: 30px;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding: 0 34px;
      .search-icon {
        width: 35px;
        height: 35px;
        margin-left: 100px;
        transition: margin 300ms;
        img {
          width: 100%;
        }
      }
      .searchInput {
        width: 100%;
        box-sizing: border-box;
        padding-right: 40px;
        color: #fff;
      }
      .clearInput {
        position: absolute;
        background: url('../../assets/img/movie/white_clear.png') no-repeat center;
        background-size: 28px 28px;
        position: absolute;
        right: 10px;
        width: 60px;
        height: 60px;
      }
    }
    .searchButton {
      width: 100px;
      text-align: center;
      font-size: 28px;
      color: #fff;
    }
  }
}
#articleSearch {
  height: 100vh;
  overflow: auto;
  padding-top: 88px;
  box-sizing: border-box;
  .searchTip {
    .search-item {
      background: #FFFFFF;
      box-shadow: 0 1px 0 0 #F5F5F5;
      height: 90px;
      line-height: 1.5;
      display: flex;
      align-items: center;
      padding: 0 30px;
      font-size: 30px;
      .text {
        flex: 1;
        min-width: 0;
        box-sizing: border-box;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
  }
  .scrollTouch {
    padding-bottom: 120px;
  }
  .noResult {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    padding: 90px 0;
    .iconSize {
      width: 142px;
      height: 142px;
    }
    .text {
      margin-top: 30px;
      line-height: 45px;
      font-size: 32px;
      color: #666666;
    }
  }
  // 历史记录
  .history-title {
    padding: 41px 32px 23px;
    .text {
      line-height: 45px;
      color: #666;
      font-size: 32px;
    }
    .rightIcon {
      width: 32px;
      height: 34px;
      img {
        width: 100%;
      }
    }
  }
  .history-list {
    .history-item {
      display: flex;
      font-size: 32px;
      height: 105px;
      line-height: 45px;
      padding: 0 32px;
      box-sizing: border-box;
      align-items: center;
      color: #333333;
      .timeIcon {
        width: 35px;
        height: 35px;
        margin-right: 14px;
      }
      .text {
        min-width: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        min-width: 0;
        flex: 1;
        white-space: nowrap;
      }
    }
  }
  .no-list-data {
    font-size: 28px;
  }
  // 暂无数据处理
  .no-history {
    text-align: center;
    overflow: hidden;
    .list-image {
      margin: 162px auto 30px;
      width: 160px;
      height: 143px;
    }
    .text {
      font-size: 30px;
      font-size: 32px;
      color: #666666;
    }
  }
}
</style>



